<template>
  <div class="creatGroup">
    <header-view :headerObj="{title:'群成员',count:0}"></header-view>
    <div>
      <div class="groupImg">
        <table>
          <template v-if="contactsSelected.length===3">
            <tr class="length3">
              <td rowspan="2" class="left">
                <img v-if="contactsSelected[0].imgSrc" :src="contactsSelected[0].imgSrc" alt="">
                <p v-else class="imgDiv">{{contactsSelected[0].name.substr(-1,1)}}</p>
              </td>
              <td class="right">
                <img v-if="contactsSelected[1].imgSrc" :src="contactsSelected[1].imgSrc" alt="">
                <p v-else class="imgDiv">{{contactsSelected[1].name.substr(-1,1)}}</p>
              </td>
            </tr>
            <tr>
              <td class="right">
                <img v-if="contactsSelected[2].imgSrc" :src="contactsSelected[2].imgSrc" alt="">
                <p v-else class="imgDiv">{{contactsSelected[2].name.substr(-1,1)}}</p>
              </td>
            </tr>
          </template>
          <template v-if="contactsSelected.length>3">
            <tr class="lengthLong">
              <td class="left">
                <img v-if="contactsSelected[0].imgSrc" :src="contactsSelected[0].imgSrc" alt="">
                <p v-else class="imgDiv">{{contactsSelected[0].name.substr(-1,1)}}</p>
              </td>
              <td class="right">
                <img v-if="contactsSelected[1].imgSrc" :src="contactsSelected[1].imgSrc" alt="">
                <p v-else class="imgDiv">{{contactsSelected[1].name.substr(-1,1)}}</p>
              </td>
            </tr>
            <tr>
              <td class="left">
                <img v-if="contactsSelected[2].imgSrc" :src="contactsSelected[2].imgSrc" alt="">
                <p v-else class="imgDiv">{{contactsSelected[2].name.substr(-1,1)}}</p>
              </td>
              <td class="right">
                <img v-if="contactsSelected[3].imgSrc" :src="contactsSelected[3].imgSrc" alt="">
                <p v-else class="imgDiv">{{contactsSelected[3].name.substr(-1,1)}}</p>
              </td>
            </tr>
          </template>
        </table>
      </div>
      <ul>
        <li class="groupName">
          <label for="groupName">群名称</label>
          <input id="groupName" type="text" name="groupName" v-model="groupName" placeholder="取个群名称方便后续搜索">
        </li>
        <li>
          <p>群成员 <span>{{contactsSelected.length}}人</span></p>
          <ul>
            <li v-for="(item,index) in contactsSelected" v-if="index<5">
              <img v-if="item.imgSrc" :src="item.imgSrc" alt="">
              <p v-else class="imgDiv">{{item.name.substr(-1,2)}}</p>
            </li>
            <li @click="jump('/groupChat')"><p class="imgDiv clearBack">+</p></li>
            <li @click="jump('/removeSelectedCont')"><p class="imgDiv clearBack">-</p></li>
          </ul>
        </li>
        <li class="groupType"><p>群类型 <span>普通群</span></p></li>
        <li class="warnText"><p>群成员均来自于不同企业，已为你选择普通群</p></li>
        <li>
          <div>
            新成员可查看聊天记录
            <label>
              <input type="checkbox" name="viewChat" v-model="checkboxValue">
              <span></span>
            </label>
          </div>
          </li>
        <li class="warnText"><p>新成员入群可查看最近100条聊天记录</p></li>
      </ul>
      <button @click='creatGroup("/groupChatPage")'>完成创建</button>
    </div>
  </div>
</template>

<script>
  import HeaderView from '../../../components/appStore/header-x'
  import {mapState,mapMutations} from "vuex"
  export default {
    name:"creatGroupMSg",
    data(){
      return{
        checkboxValue:true,
        groupName:"",
        defaultGroupName:""
      }
    },
    created(){
      this.init();
    },
    computed:{
      ...mapState(["contactsSelected"])
    },
    watch:{
    },
    components:{HeaderView},
    methods:{
      init(){
        var a=this.contactsSelected.length
        if(a===1){
          this.$router.push({path:'/'})
        }else if(a===2){
          // 两人聊天的逻辑还需处理
          this.$router.push({path:'/groupChatPage'})
        }
      },
      ...mapMutations(["updateGroupMsg","addGroupList","initSelectedContacts"]),
      jump(url){
        this.$router.push({path:url})
      },
      creatGroup(url){
        console.log("watchName",this.groupName);
        if(!this.groupName){
          var arr=this.contactsSelected;
          for (var i = 1; i < arr.length && i<4; i++) {
            this.defaultGroupName+=arr[i].name+","
          }
          this.defaultGroupName+=arr[0].name;
        }else{
          this.defaultGroupName=this.groupName
        }
        console.log("groupname",this.defaultGroupName);

        var val={
          name:this.defaultGroupName,
          imgSrc:'',
          memberList:this.contactsSelected
        };
        this.updateGroupMsg(val)
        this.$router.push({path:url})
        this.addGroupList(val)
        this.initSelectedContacts()
      }
    }
  }
</script>

<style lang="less" scoped>
  @import '../../../public/comment.less';
  div.creatGroup{
    padding-top:2rem;
    >div{
      .groupImg{
        padding:.5rem;
        >table{
          width: 80px;
          height: 80px;
          margin:0 auto;
          border-radius: 50%;
          overflow: hidden;

            tr:first-child>td:nth-child(1)>.imgDiv{background:#16C295;}
            tr:first-child>td:nth-child(2)>.imgDiv{background:#F8B45E;}
            tr:last-child>td:nth-child(1)>.imgDiv{background:#F2725E;}
            tr:last-child>td:nth-child(2)>.imgDiv{background:#38ADFF;}

          >tr{
            &.length3{
              >td.left>.imgDiv{line-height: 76px;}
            }
            >td{
              width: 50%;
              >img{
                width: 100%;
              }
              >.imgDiv{
                height: 100%;
                width: 100%;
                line-height: 37px;
                font-size:18px;
                color:@white;
              }
              &.left>.imgDiv{padding-left: 12px;}
              &.right>.imgDiv{padding-left: 5px;}
            }
          }
        }
      }
      >ul>li{
        border-top:1px solid @borderColor;
        padding:.5rem;
        &:nth-child(even){background:@borderColor;}
        &.groupName{
          label{font-size: 16px;}
          input{
            height: 1rem;
          }
        }
        &.groupName+li{span{float: right;}}
        >ul{
        overflow: hidden;
          >li{
            float: left;
            padding:.3rem;
            >img{
                vertical-align:middle;
                width:1.5rem;
                height:1.5rem;
              }
            >.imgDiv{
                display:inline-block;
                width:1.5rem;
                height:1.5rem;
                line-height: 1.5rem;
                border-radius:50%;
                text-align: center;
                vertical-align:middle;
                color:#fff;
                font-size:12px;
            }
            &:nth-child(1)>.imgDiv{background:#16C295;}
            &:nth-child(2)>.imgDiv{background:#F8B45E;}
            &:nth-child(3)>.imgDiv{background:#F2725E;}
            &:nth-child(4)>.imgDiv{background:#38ADFF;}
            &:nth-child(5)>.imgDiv{background:#16C295;}
            &:nth-child(6)>.imgDiv{background:#16C295;}
            .clearBack{
              background: #fff !important;
              color:#aaa;
              font-size: 22px;
              border:1px solid #aaa;
              line-height: 1.3rem;
            }
          }
        }
        &.groupType{
          span{
            float: right;
          }
        }
        &.warnText{color:@gray;}
        >div{
          height: 22px;
          line-height: 22px;
          label{
            float: right;
            >span{
              display: inline-block;
              position: relative;
              width: 52px;
              height: 22px;
              border: 1px solid #d9d9d9;
              border-radius: 16px;
              box-sizing: border-box;
              background: #d9d9d9;
              &:after{
                content: " ";
                top: 0;
                left: 0;
                position: absolute;
                transition: transform .3s;
                border-radius: 15px;
                width: 30px;
                height: 20px;
                background-color: #fff;
                box-shadow: 0 1px 3px rgba(0, 0, 0, .4);
              }
            }
            input{display: none;}
            input:checked+span{
              border-color: @lightBlue;
              background-color: @lightBlue;
              &:after{
                transform:translateX(20px);
              }
            }
          }
        }
      }
      >button{
        width: 100%;
        position: fixed;
        left:0;
        bottom:0;
        height: 2rem;
        background: @lightBlue;
        color:@white;
      }
    }

  }
</style>



